function Metrics (props) {
	// eslint-disable-next-line no-unused-vars
	const { metrics, metricsToShow, setMetricsToShow } = props;
	
	const handleToggleMetricToAdd = (e, metric) => {
		const value = e.target.value;
		console.log({value: e.target.value, metric});
		let updatedMetrics;
		if (value === '1') {
			updatedMetrics =  [...metricsToShow, metric];
			// set elemnt to active	
		}
		if (value === '2') {
			updatedMetrics =  metricsToShow.filter((m => m.id !== metric.id));
			// set elemnt to inactive
		}
		console.log('updatedMetrics', updatedMetrics);
		setMetricsToShow(updatedMetrics);
	};
	console.log(handleToggleMetricToAdd);

	const handleDefaultMetricButtonValue = (metric) => {
		const relevantMetric  = metricsToShow.find(m => m.id === metric.id);
		return relevantMetric ? 1 : 2;
	};
	// const setDefaultMetricButtonActive = (metric) => {
	// 	const res =  metricsToShow.filter(m => m.id === metric.id)[0] ? 1 : 2;
	// 	console.log(res);
	// 	return res;
	// };
	
	return (
		<Table striped bordered hover>
			<thead>
				<tr>
					<th>Saved Metrics</th>
					<th>Add Metric to Class?</th>
					<th className='text-center'>Delete Metric</th>
				</tr>
			</thead>
			<tbody>
				{metrics && (metrics.map((metric) => {
					// console.log({metrics,metricsToShow});
					// console.log('value: ', handleDefaultMetricButtonValue(metric));
					// setDefaultMetricButtonActive(metric);
					return (
						<tr key={metric.name}>
							<td>{metric.name}</td>
							<td>
								<ButtonGroup
									// type="radio" 
									// name="options"
									// value={handleDefaultMetricButtonValue(metric)}
									// defaultValue={handleDefaultMetricButtonValue(metric)}
									// onChange={e => handleToggleMetricToAdd(e, metric)}
								>
									<ToggleButton
										type="radio" 
										name="options"
										checked={handleDefaultMetricButtonValue(metric) === 1}
										// defaultValue={handleDefaultMetricButtonValue(metric)}
										onChange={e => handleToggleMetricToAdd(e, metric)}
										id={`show-metric-btn-${metric.id}`} variant='outline-dark' value={1}>
										Yes
									</ToggleButton>
									<ToggleButton
										type="radio" 
										name="options"
										checked={handleDefaultMetricButtonValue(metric) === 2}
										// defaultValue={handleDefaultMetricButtonValue(metric)}
										onChange={e => handleToggleMetricToAdd(e, metric)}
										id={`hide-metric-btn-${metric.id}`} variant='outline-dark' value={2}>
										No
									</ToggleButton>
								</ButtonGroup>
							</td>
							<td>
								<Col className='center-vertically center-horizontally'>
									<Button
										variant='outline-dark'
										onClick={() => {console.log('delet');}}
									>
										<XCircle size={25} />
									</Button>
								</Col>
							</td>
						</tr>
					);
				}))}
			</tbody>
		</Table>
	);
}

export default Metrics;

